import React from 'react'
import { useSelector, useDispatch } from "react-redux";
import {formValueSelector, changeFormValueAction} from "../../../store/slices/createNewShopFormValueSlice";
import getRequest from '../../../services/requests/getRequest'
import {shopUrlMaker} from "../../../helpers/shopUrlMaker";

const ShopLink = props => {

    const [loading, setLoading] = React.useState(false)

    const formValues = useSelector(formValueSelector)
    const dispatch = useDispatch();
    const [errors, setErrors] = React.useState({slug: ''})

    const continueProcess = e => {

        e.preventDefault();
        if(errors.slug){
            return
        }

        if(!formValues?.slug){
            return setErrors({slug: 'This field is required!'})

        }

        setLoading(true)
        getRequest('/v1/shop/check-slug/' + formValues.slug, {})
            .then(res => {
                setLoading(false)
                setErrors('')
                props.nextStep()
            })
            .catch(err => {
                setLoading(false)
                setErrors({slug: err?.message || 'Must need a unique value!'})
            })
    };
    const back = e => {
        e.preventDefault();
        props.prevStep();
    };
    const changeSlug = e => {
        const value = e.target.value;
        dispatch(changeFormValueAction({name: 'slug', value: value}))
        if(value.includes(' ')){
            return setErrors({slug: 'White space are not allowed!'})
        }
        if(value.length < 3 || value.length > 20){
            return setErrors({slug: 'Only 3 to 20 character allow!'})
        }else{
            setErrors({slug: ''})
        }

    }

    return (
        <div className="md:flex justify-center items-center min-h-screen bg-gray-100 px-8 md:px-0">
            <div className="flex-col grid md:grid-cols-2 gap-10">
                <div className="pl-4 pt-10 mt-32 md:mt-0">
                    <p className="text-yellow-400 text-xs">7 of 10</p>
                    <div className="mt-5 md:mt-0 md:h-80 grid items-center">
                        <div>
                            <div className="mb-3 md:px-5 pt-0 relative">
                                <div className="w-4 max-h-10 absolute md:-left-0 -left-7 top-2 ">
                                    <div
                                        className="w-5 h-5 bg-yellow-400 rounded-full flex justify-center items-center">
                                        <p className="text-white">7</p>
                                    </div>
                                    <img className="h-24 w-10 rounded" src='/assets/svgs/createshop/line.svg' alt=""/>
                                </div>
                                <h3 className="py-2 ml-1">What would be the link of the shop?</h3>
                                <p className="text-xs text-gray-500 pb-2">You can use the link to invite people.</p>
                                <input
                                    type="text"
                                    onChange={changeSlug}
                                    value={formValues?.slug || ''}
                                    placeholder="ex: kortex-shop"
                                    className="form-control px-3 py-2 placeholder-blueGray-300
                                text-blueGray-600 max-w-md relative bg-white rounded
                                text-sm border border-gray-300
                                outline-none focus:outline-none shadow sm:w-screen w-full"
                                />
                                {
                                    errors.slug ? (
                                        <p className="text-xs font-bold text-red-600 pt-1 pl-2">{errors.slug}</p>
                                    ): (
                                        formValues?.slug ? <p className="text-sm font-bold text-gray-700 pt-1 pl-2">{shopUrlMaker(formValues?.slug)}</p> : ''

                                    )
                                }

                            </div>
                        </div>
                    </div>
                    <div className="flex justify-between md:px-5 pt-5 md:pt-0">
                        <button onClick={back}
                                className="bg-gray-700 text-white text-xs
                                        py-2 rounded hover:bg-gray-600 w-24"
                        >Back
                        </button>
                        <button
                            className="bg-button flex justify-center items-center text-white text-xs
                                        py-2 rounded hover:bg-blue-600 w-24"
                            onClick={continueProcess}
                        >
                            {
                                loading ? (
                                    <svg className="animate-spin -ml-1 mr-3 h-5 w-5 text-white"
                                         xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                                        <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor"
                                                strokeWidth="4"></circle>
                                        <path className="opacity-75" fill="currentColor"
                                              d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                                    </svg>
                                ) : ''
                            }
                            NEXT
                        </button>
                    </div>
                </div>

            </div>
        </div>
    )
}

export default ShopLink
